.info-tooltip{
    width: 100%;
    display: flex;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    position: fixed;
    transition: visibility .5s,opacity .5s linear;
    background: rgba(0,0,0,.5);
}